<template>
  <div>
    <el-form
      class="detail-form-content"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="80px"
    >
            <el-row>
                                    <el-col :span="12">

                                  <el-form-item  v-if="type!='info'"  label="" prop="yishenggonghao">
                                      <el-input v-model="ruleForm.yishenggonghao"
                                                placeholder="" clearable></el-input>
                                  </el-form-item>
                                  <div v-else>
                                      <el-form-item v-if="ruleForm.yishenggonghao" label="" prop="yishenggonghao">
                                          <el-input v-model="ruleForm.yishenggonghao"
                                                    placeholder="" readonly></el-input>
                                      </el-form-item>
                                  </div>
                      </el-col>
                      <el-col :span="12">

                                  <el-form-item  v-if="type!='info'"  label="" prop="yishengxingming">
                                      <el-input v-model="ruleForm.yishengxingming"
                                                placeholder="" clearable></el-input>
                                  </el-form-item>
                                  <div v-else>
                                      <el-form-item v-if="ruleForm.yishengxingming" label="" prop="yishengxingming">
                                          <el-input v-model="ruleForm.yishengxingming"
                                                    placeholder="" readonly></el-input>
                                      </el-form-item>
                                  </div>
                      </el-col>
                      <el-col :span="12">

                                  <el-form-item  v-if="type!='info'"  label="" prop="zhicheng">
                                      <el-input v-model="ruleForm.zhicheng"
                                                placeholder="" clearable></el-input>
                                  </el-form-item>
                                  <div v-else>
                                      <el-form-item v-if="ruleForm.zhicheng" label="" prop="zhicheng">
                                          <el-input v-model="ruleForm.zhicheng"
                                                    placeholder="" readonly></el-input>
                                      </el-form-item>
                                  </div>
                      </el-col>
                      <el-col :span="12">
                                <el-form-item label="" prop="keshi">
                                    <el-select v-model="ruleForm.keshi" placeholder="">
                                        <el-option
                                                v-for="(item,index) in keshiOptions"
                                                v-bind:key="index"
                                                :label="item"
                                                :value="item">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                      </el-col>
                      <el-col :span="12">

                                  <el-form-item  v-if="type!='info'"  label="" prop="chuzhenshijian">
                                      <el-input v-model="ruleForm.chuzhenshijian"
                                                placeholder="" clearable></el-input>
                                  </el-form-item>
                                  <div v-else>
                                      <el-form-item v-if="ruleForm.chuzhenshijian" label="" prop="chuzhenshijian">
                                          <el-input v-model="ruleForm.chuzhenshijian"
                                                    placeholder="" readonly></el-input>
                                      </el-form-item>
                                  </div>
                      </el-col>
                      <el-col :span="12">

                                  <el-form-item  v-if="type!='info'"  label="" prop="shijianduan">
                                      <el-input v-model="ruleForm.shijianduan"
                                                placeholder="" clearable></el-input>
                                  </el-form-item>
                                  <div v-else>
                                      <el-form-item v-if="ruleForm.shijianduan" label="" prop="shijianduan">
                                          <el-input v-model="ruleForm.shijianduan"
                                                    placeholder="" readonly></el-input>
                                      </el-form-item>
                                  </div>
                      </el-col>
                      <el-col :span="12">

                                  <el-form-item  v-if="type!='info'"  label="" prop="taidupingfen">
                                      <el-input v-model="ruleForm.taidupingfen"
                                                placeholder="" clearable></el-input>
                                  </el-form-item>
                                  <div v-else>
                                      <el-form-item v-if="ruleForm.taidupingfen" label="" prop="taidupingfen">
                                          <el-input v-model="ruleForm.taidupingfen"
                                                    placeholder="" readonly></el-input>
                                      </el-form-item>
                                  </div>
                      </el-col>
                      <el-col :span="12">

                                  <el-form-item  v-if="type!='info'"  label="" prop="jishupingfen">
                                      <el-input v-model="ruleForm.jishupingfen"
                                                placeholder="" clearable></el-input>
                                  </el-form-item>
                                  <div v-else>
                                      <el-form-item v-if="ruleForm.jishupingfen" label="" prop="jishupingfen">
                                          <el-input v-model="ruleForm.jishupingfen"
                                                    placeholder="" readonly></el-input>
                                      </el-form-item>
                                  </div>
                      </el-col>
                      <el-col :span="12">
                                <el-form-item v-if="type!='info'" label="" prop="pingjianeirong">
                                    <editor
                                            v-model="ruleForm.pingjianeirong"
                                            class="editor"
                                            action="file/upload">
                                    </editor>
                                </el-form-item>
                              <div v-else>
                                  <el-form-item v-if="ruleForm.pingjianeirong" label="" prop="pingjianeirong">
                                      <span v-html="ruleForm.pingjianeirong"></span>
                                  </el-form-item>
                              </div>
                      </el-col>
                      <el-col :span="12">
                                <el-form-item label="" prop="pingjiariqi">
                                    <el-date-picker
                                            format="yyyy  MM  dd "
                                            value-format="yyyy-MM-dd HH:mm:ss"
                                            v-model="ruleForm.pingjiariqi"
                                            type="date"
                                            placeholder="">
                                    </el-date-picker>
                                </el-form-item>
                      </el-col>
                      <el-col :span="12">

                                  <el-form-item  v-if="type!='info'"  label="" prop="yonghuming">
                                      <el-input v-model="ruleForm.yonghuming"
                                                placeholder="" clearable></el-input>
                                  </el-form-item>
                                  <div v-else>
                                      <el-form-item v-if="ruleForm.yonghuming" label="" prop="yonghuming">
                                          <el-input v-model="ruleForm.yonghuming"
                                                    placeholder="" readonly></el-input>
                                      </el-form-item>
                                  </div>
                      </el-col>
                      <el-col :span="12">

                                  <el-form-item  v-if="type!='info'"  label="" prop="xingming">
                                      <el-input v-model="ruleForm.xingming"
                                                placeholder="" clearable></el-input>
                                  </el-form-item>
                                  <div v-else>
                                      <el-form-item v-if="ruleForm.xingming" label="" prop="xingming">
                                          <el-input v-model="ruleForm.xingming"
                                                    placeholder="" readonly></el-input>
                                      </el-form-item>
                                  </div>
                      </el-col>
                      <el-col :span="12">
                                <el-form-item label="" prop="sfsh">
                                    <el-select v-model="ruleForm.sfsh" placeholder="">
                                        <el-option key="" label="" value=""></el-option>
                                        <el-option key="" label="" value=""></el-option>
                                    </el-select>
                                </el-form-item>
                      </el-col>
                      <el-col :span="12">

                                  <el-form-item  v-if="type!='info'"  label="" prop="shhf">
                                      <el-input v-model="ruleForm.shhf"
                                                placeholder="" clearable></el-input>
                                  </el-form-item>
                                  <div v-else>
                                      <el-form-item v-if="ruleForm.shhf" label="" prop="shhf">
                                          <el-input v-model="ruleForm.shhf"
                                                    placeholder="" readonly></el-input>
                                      </el-form-item>
                                  </div>
                      </el-col>

            </el-row>

        <el-form-item>
            <el-button v-if="type!='info'" type="primary" @click="onSubmit"></el-button>
            <el-button v-if="type!='info'" @click="back()"></el-button>
            <el-button v-if="type=='info'" @click="back()"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isMobile,isURL,checkIdCard } from "@/utils/validate";
export default {
  data() {
    var validateIdCard = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!checkIdCard(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateUrl = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isURL(value)) {
        callback(new Error("URL"));
      } else {
        callback();
      }
    };
    var validatePhone = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isMobile(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateEmail = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isEmail(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateNumber = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isNumber(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateIntNumber = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isIntNumer(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };

    return {
      id: '',
      type: '',
      ruleForm: {
                      addtime: '',
                      yishenggonghao: '',
                      yishengxingming: '',
                      zhicheng: '',
                      keshi: '',
                      chuzhenshijian: '',
                      shijianduan: '',
                      taidupingfen: '',
                      jishupingfen: '',
                      pingjianeirong: '',
                      pingjiariqi: '',
                      yonghuming: '',
                      xingming: '',
                      sfsh: '',
                      shhf: '',
      },

                    keshiOptions: [],
        rules: {
                        addtime: [
                        ],
                        yishenggonghao: [
                            { required: true, message: '', trigger: 'blur' },
                        ],
                        yishengxingming: [
                            { required: true, message: '', trigger: 'blur' },
                        ],
                        zhicheng: [
                        ],
                        keshi: [
                            { required: true, message: '', trigger: 'blur' },
                        ],
                        chuzhenshijian: [
                        ],
                        shijianduan: [
                        ],
                        taidupingfen: [
                        ],
                        jishupingfen: [
                        ],
                        pingjianeirong: [
                        ],
                        pingjiariqi: [
                        ],
                        yonghuming: [
                            { required: true, message: '', trigger: 'blur' },
                        ],
                        xingming: [
                        ],
                        sfsh: [
                        ],
                        shhf: [
                        ],
        }
    };
  },
  props: ["parent"],
  computed: {
  },
  methods: {
      // 
      download(file) {
          window.open(`${file}`)
      },
      // 
      init(id, type) {
          if (id) {
              this.id = id;
              this.type = type;
          }
          if (this.type == 'info' || this.type == 'else') {
              this.info(id);
          } else if (this.type == 'cross') {
              var obj = this.$storage.getObj('crossObj');
              for (var o in obj) {
                        if (o == 'id') {
                            this.ruleForm.id = obj[o];
                            continue;
                        }
                        if (o == 'addtime') {
                            this.ruleForm.addtime = obj[o];
                            continue;
                        }
                        if (o == 'yishenggonghao') {
                            this.ruleForm.yishenggonghao = obj[o];
                            continue;
                        }
                        if (o == 'yishengxingming') {
                            this.ruleForm.yishengxingming = obj[o];
                            continue;
                        }
                        if (o == 'zhicheng') {
                            this.ruleForm.zhicheng = obj[o];
                            continue;
                        }
                        if (o == 'keshi') {
                            this.ruleForm.keshi = obj[o];
                            continue;
                        }
                        if (o == 'chuzhenshijian') {
                            this.ruleForm.chuzhenshijian = obj[o];
                            continue;
                        }
                        if (o == 'shijianduan') {
                            this.ruleForm.shijianduan = obj[o];
                            continue;
                        }
                        if (o == 'taidupingfen') {
                            this.ruleForm.taidupingfen = obj[o];
                            continue;
                        }
                        if (o == 'jishupingfen') {
                            this.ruleForm.jishupingfen = obj[o];
                            continue;
                        }
                        if (o == 'pingjianeirong') {
                            this.ruleForm.pingjianeirong = obj[o];
                            continue;
                        }
                        if (o == 'pingjiariqi') {
                            this.ruleForm.pingjiariqi = obj[o];
                            continue;
                        }
                        if (o == 'yonghuming') {
                            this.ruleForm.yonghuming = obj[o];
                            continue;
                        }
                        if (o == 'xingming') {
                            this.ruleForm.xingming = obj[o];
                            continue;
                        }
                        if (o == 'sfsh') {
                            this.ruleForm.sfsh = obj[o];
                            continue;
                        }
                        if (o == 'shhf') {
                            this.ruleForm.shhf = obj[o];
                            continue;
                        }

              }
          }
                      this.$http({
                          url: `option/keshixinxi/keshi`,
                          method: "get"
                      }).then(({ data }) => {
                          if (data && data.code === 0) {
                              this.keshiOptions = data.data;
                          } else {
                              this.$message.error(data.msg);
                          }
                      });
      },

      info(id) {
          this.$http({
              url: `jiuzhenpingjia/info/${id}`,
              method: "get"
          }).then(({data}) => {
              if (data && data.code === 0) {
                  this.ruleForm = data.data;
              } else {
                  this.$message.error(data.msg);
              }
          });
      },
      // 
      onSubmit() {
          this.$refs["ruleForm"].validate(valid => {
              if (valid) {
                  this.$http({
                      url: `jiuzhenpingjia/${!this.ruleForm.id ? "save" : "update"}`,
                      method: "post",
                      data: this.ruleForm
                  }).then(({data}) => {
                      if (data && data.code === 0) {
                          this.$message({
                              message: "",
                              type: "success",
                              duration: 1500,
                              onClose: () => {
                                  this.parent.showFlag = true;
                                  this.parent.addOrUpdateFlag = false;
                                  this.parent.jiuzhenpingjiaCrossAddOrUpdateFlag = false;
                                  this.parent.search();
                              }
                          });
                      } else {
                          this.$message.error(data.msg);
                      }
                  });
              }
          });
      },
      // uuid
      getUUID() {
          return new Date().getTime();
      },
      // 
      back() {
          this.parent.showFlag = true;
          this.parent.addOrUpdateFlag = false;
          this.parent.jiuzhenpingjiaCrossAddOrUpdateFlag = false;
      },
  }
};
</script>
<style lang="scss" scoped>
.editor{
  height: 500px;
}
</style>
